.fullLogPage {
	height: 100vh;
	width: 100vw;
	background: red;
	padding: 0;
	display: flex;
	flex-direction: column;
}

.headerLogPage {
	height: 20vh;
	width: 100vw;
	background: #00309c;
	/* border-bottom: 1px solid white; */
	border: 1px white solid;
	/* border-image-: linear-gradient(to left, red, rgba(0, 0, 0, 0)) ; */
	border-left: 0;
	border-right: 0;
	border-top: 0;
}

.mainLogPage {
	height: 70vh;
	width: 100vw;
	background: #5a7edc;
	display: flex;
}
.footerLogPage {
	height: 20vh;
	width: 100vw;
	background: #00309c;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.buttonRestart {
	background: none;
	height: 5vw;
	width: 30vw;
	display: flex;
	align-items: center;
	font-size: 1.3vw;
	border: none;
	color: white;
	margin-top: 1vw;
	margin-left: 1vw;
	cursor: pointer;
}

.pictureRestart {
	height: 80%;
}

.footerText {
	text-align: center;
	margin-right: 1vw;
	color: white;
	font-size: 1vw;
	width: 20vw;
	display: flex;
	flex-wrap: wrap;
}

.leftSideLogPage {
	height: 100%;
	width: 50%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: end;
	/* background: red; */
}

.rightSideLogPage {
	height: 100%;
	width: 50%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	/* align-items: center; */
	/* background: blue; */
	border-left: 1px white solid;
}

.logo_windowsXpLogPage {
	height: 10vw;
	width: 15vw;
	margin-right: 2vw;
}

.titleLogPage {
	color: white;
	font-size: 2vw;
	margin-right: 2vw;
}

.pictureUser_logPage {
  height: 6vw;
  width: 6vw;
  background-image: url("../assets/images/picture_Mouad.jpg");
  background-repeat: no-repeat;

  background-size: 135%;
  background-position: center;

  margin-left: 2vw;
  border: 2px solid white;
  border-radius: 3px;
}


.nameUser_logPage {
	font-size: 1.5vw;
	color: white;
	font-weight: bold;
	/* margin-left: 3%; */
	/* margin-left: -40%; */
	/* margin-top: -12%; */
}

.profils {
	height: 8vw;
	width: 100%;
	/* background: red; */
	display: flex;
	align-items: center;
}

.profils:hover {
	background: #1440b5;
	cursor: pointer;
}

.infosUser_logPage {
	width: 80%;
	height: 6vw;
	/* background: green; */
	display: flex;
	flex-direction: column;
	align-items: start;
	padding-left: 3%;
}

.password_logPage {
	color: white;
	font-size: 0.6vw;
	margin-top: 2%;
}

.inputs {
	width: 100%;
	height: 30%;
	display: flex;
	justify-content: start;
	/* background: yellowgreen; */
}

.inputPassword_logPage {
	margin-top: 1%;
	height: 100%;
	width: 50%;
	border-radius: 5px;
	outline: none;
	font-size: 1vw;
}

.logoNext {
	/* height: 6vw; */
	width: 2vw;
	height: 2vw;
	/* background: red; */
	background: url("../assets/images/arrow.png");
	background-size: cover;
	background-position: center;
	border: 1px white solid;
	border-radius: 10px;
	margin-top: 1%;
	margin-left: 2%;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.logoNext:hover {
	cursor: pointer;
	opacity: 0.8;
}

